<template>
<div class="uploader-wrapper">
    <!-- <slot></slot> this is slot for any template UI -->
    <input :type="`file`" class="file-input-hidden invisible" @change="changeLocal" :data-filename="filename || defaultFilename" ref="inputFile" :accept="accept">
    <span class="default-file-input-button" @click="triggerInput">{{filename || defaultFilename}} {{isUploading ? ` - ${uploadProgress}%` : ''}}</span>
</div>
</template>

<script>
import {
    DO_UPLOAD,
    DO_UPLOAD_RESET,
} from '@/mutations/Upload.mutation';
export default {
    name: 'FileUpload',
    props: {
        value: String,
        accept: {
            type: String,
            default: '.png,.jpg,.jpeg'
        }
    },
    model: {
        event: 'change'
    },
    data: function () {
        return {
            filename: '',
            defaultFilename: 'File...',
            files: [],
        }
    },
    created: function () {
        this.$store.commit(DO_UPLOAD_RESET);
    },
    methods: {
        changeLocal: async function (event) {
            this.files = this.$refs.inputFile.files;
            if (this.files.length > 0) {
                this.filename = this.files[0].name
                await this.$store.dispatch(DO_UPLOAD, this.files)
                this.$emit('change', this.$store.state.Upload.uploadedPath);
            }
        },
        triggerInput: function () {
            this.$refs.inputFile.click();
        }
    },
    computed: {
        uploadProgress: function () {
            return this.$store.state.Upload.progress
        },
        isUploading: function () {
            return this.$store.state.Upload.isUploading
        },
        uploadedPath: function () {
            return this.$store.state.Upload.uploadedPath
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.uploader-wrapper {
    position: relative;
    display: inline-flex;

    .file-input-hidden {
        display: inline;
        position: absolute;
        top: 0;
        left: 0;
    }

    // This is about slot css
    .default-file-input-button {
        border: 1px solid $cy-blue;
        box-sizing: border-box;
        border-radius: 4px;
        font-family: NotoSansKR;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 19px;
        display: flex;
        align-items: center;
        text-align: center;
        color: $cy-blue;
        padding: 0 15px;
        height: 30px;
    }
}
</style>
