<template>
<div class="container-fluid join-step-wrapper pt-0">
    <div class="row join-step-row-content full-width">
        <FileInputFull isRequired v-model="FormData.resumeFile" :title="$t('Joining.UploadResume')" :description="$t('Joining.UploadDesc')" :wrapperClass="'w-100'" :placeholder="''" />
    </div>
    <Gap :height="'1.1rem'" />
    <div class="row m-0 join-step-row-content">
        <RadioInput isRequired :label="$t('Joining.CurrentStatus')" v-model="FormData.userStatus"
                    :options="[{name: $t(`Joining.${$CYconstants.STATUS.IN_PROGRESS}`), value: $CYconstants.STATUS.IN_PROGRESS}, {name: $t(`Joining.${$CYconstants.STATUS.WAITING}`), value: $CYconstants.STATUS.WAITING}]" :name="'statusSelector'" :value="$CYconstants.STATUS.WAITING"></RadioInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <DateTimeInput isRequired :label="$t('Joining.AvailableDate')" v-model="FormData.availDt"></DateTimeInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.JobOfHope1')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="Jobs" v-model="FormData.job11"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput :options="Jobs" v-model="FormData.job12"/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.JobOfHope2')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="Jobs" v-model="FormData.job21"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput :options="Jobs" v-model="FormData.job22"/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.HopefulWorkPlace1')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="Places" v-model="FormData.location11"/>
        </div>
        <div class="col p-0 cy-col-pad">
            <SelectInput :options="Places" v-model="FormData.location12"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput :options="Places" v-model="FormData.location13"/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.HopefulWorkPlace2')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput :options="Places" v-model="FormData.location21"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <TextInput v-model="FormData.location22" :name='"HopefulWorkPlace2Two"'/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.TotalITExperience')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput v-model="FormData.itCareerYear" :options="Years"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput v-model="FormData.itCareerMonth" :options="Months"/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <div class="col-12 p-0">
            <InputLabel isRequired :label="$t('Joining.SAPExperience')" />
        </div>
        <div class="col p-0 cy-col-pad first">
            <SelectInput v-model="FormData.sapCareerYear" :options="Years"/>
        </div>
        <div class="col p-0 cy-col-pad last">
            <SelectInput v-model="FormData.sapCareerMonth" :options="Months"/>
        </div>
    </div>
    <div class="row m-0 join-step-row-content">
        <RadioInput isRequired :label="$t('LanguageSkills.ENGLISH')" v-model="FormData.langEn" 
            :options="[
                {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.HIGH}`), value: $CYconstants.LANGUAGE_SKILLS.HIGH},
                {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.MIDDLE}`), value: $CYconstants.LANGUAGE_SKILLS.MIDDLE},
                {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.LOW}`), value: $CYconstants.LANGUAGE_SKILLS.LOW},
                {name: $t(`LanguageSkills.${$CYconstants.LANGUAGE_SKILLS.NONE}`), value: $CYconstants.LANGUAGE_SKILLS.NONE},
            ]" 
            :name="'english'" :hasError="false"></RadioInput>
    </div>
    <div class="row m-0 join-step-row-content">
        <CheckboxInput :label="$t('Joining.TermsAndConds')" :name="'termsnconds'"/>
    </div>
    <div class="row m-0 join-step-row-content">
        <Button :label="$t('Joining.JoinComplete')" :wrapperClass="'w-100'" @click.native="goToPersonalInformation"></Button>
    </div>
</div>
</template>

<script>
import TextInput from '@/components/Common/Input/TextInput';
import DateTimeInput from '@/components/Common/Input/DateTimeInput';
import RadioInput from '@/components/Common/Input/RadioInput';
import Button from '@/components/Common/Button/Button';
import FileInputFull from '@/components/Common/Input/FileInputFull';
import SelectInput from '@/components/Common/Input/SelectInput';
import InputLabel from '@/components/Common/Input/InputLabel';
import CheckboxInput from '@/components/Common/Input/CheckboxInput';
import Gap from '@/components/Common/Space/Gap';

export default {
    name: "StepTwo",
    components: {
        TextInput,
        DateTimeInput,
        RadioInput,
        FileInputFull,
        Button,
        SelectInput,
        Gap,
        InputLabel,
        CheckboxInput,
    },
    data: function () {
        return {
            FormData: {
                resumeFile: '',
                userStatus: this.$CYconstants.STATUS.WAITING,
                availDt: '',
                job11: '',
                job21: '',
                job12:'',
                job22: '',
                location11: '',
                location12: '',
                location13: '',
                location21: '',
                location22: '',
                itCareerYear: '',
                itCareerMonth: '',
                sapCareerMonth: '',
                sapCareerYear: '',
                select: '',
                langEn: this.$CYconstants.LANGUAGE_SKILLS.MIDDLE
            },
            Jobs: [],
            Places: [],
            Months: [],
            Years: [],
        }
    },
    created: function () {
        this.getCommonInfo();
    },
    methods: {
        goToPersonalInformation: function () {
            return this.$router.push('/personal-information');
        },
        getCommonInfo: async function () {
            let locale = this.$i18n.locale;
            let jobs = await this.$ApiService.doGetCommonInfo("B00000");
            let places = await this.$ApiService.doGetCommonInfo("C00000");
            if (jobs && jobs.data && jobs.data.RST_DATA) {
                this.Jobs = jobs.data.RST_DATA.map(item => {
                    return {
                        value: item.cmmCd,
                        name: item[`name${this.capitalizeFirstLetter(locale)}`]
                    }
                })
            }
            if (places && places.data && places.data.RST_DATA) {
                this.Places = places.data.RST_DATA.map(item => {
                    return {
                        value: item.cmmCd,
                        name: item[`name${this.capitalizeFirstLetter(locale)}`]
                    }
                })
            }

            this.Months = this.$CYconstants.MONTHS.map(m => ({value: m + "", name: m + ""}));
            this.Years = this.$CYconstants.YEARS.map(y => ({value: y + "", name: y + ""}));
        },
        capitalizeFirstLetter(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        }
    }
}
</script>

<style lang="scss">
.join-step-wrapper {
    .cy-col-pad {
        &.first {
            margin-left: 0;
        }

        &.last {
            margin-right: 0;
        }

        margin-left: .75rem;
        margin-right: .75rem;
    }
}
</style>
