<template>
<div class="d-flex flex-column checkbox-input-wrapper" :class="wrapperClass">
    <label class="checkbox-input-label">
        <input :type="`checkbox`" :value="value" @change="changeLocal" :disabled="isDisabled" :name="name">
        <span class="checkbox-input" ref="inputLabel" :id="_uid"></span>
    </label>
</div>
</template>

<script>
import Vue from 'vue';

export default {
    name: 'CheckboxInput',
    props: {
        label: String,
        value: String,
        type: String,
        error: String,
        wrapperClass: String,
        name: {
            type: String,
            required: true,
        },
        isPassword: {
            type: Boolean,
            default: false
        },
        isRequired: {
            type: Boolean,
        },
        isDisabled: {
            type: Boolean,
        },
    },
    model: {
        event: 'change'
    },
    mounted: function () {
        let self = this;
        let LabelContent = Vue.extend({
            template: this.label,
            methods: {
                gotoConds() {
                    return self.$router.push('/privacy');
                },
                gotoPolicy() {
                    return self.$router.push('/policy');
                }
            }
        })
        var component = new LabelContent().$mount();
        document.getElementById(this._uid).appendChild(component.$el);
    },
    methods: {
        changeLocal: function (event) {
            this.$emit('change', event.target.value);
        }
    },
    computed: {
        errorMessage: function () {
            let item  = this.errors.items.find(item => item.field === this.name);
            if (item) {
                return item.msg;
            } else {
                return '';
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/variables.scss';

.checkbox-input-wrapper {
    padding: 15px 0;
    .checkbox-input-label {
        font-family: NotoSansKR;
        font-weight: 300;
        font-size: 1.4rem;
        line-height: 2rem;
        color: #7C848D;
        position: relative;
        padding-left: 30px;
        input {
            position: absolute;
            width: 0;
        }
        input:checked + .checkbox-input {
            &:before {
                content: '';
                background-image: url('~@/assets/images/circle-check.png');
                display: inline-flex;
                width: 20px;
                height: 20px;
                background-size: auto;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
        .checkbox-input {
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                background-image: url('~@/assets/images/circle-uncheck.png');
                display: inline-flex;
                width: 20px;
                height: 20px;
                background-size: auto;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
}
</style>
